import styled from "styled-components";
import * as React from "react";
import { useNavigate } from "react-router-dom";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography"; //  font  
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import axios from "axios";
import crypto from 'crypto';
import { useCookies } from 'react-cookie';



const theme = createTheme();

function SignIn() {
  const navigate = useNavigate();
  const [cookies, setCookie] = useCookies(['user_id']); //   
  const address = "https://bitwise.ljlee37.com:8080"; // "https://bitwise.ljlee37.com:8080"
  const handleSubmit = async (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget); 
    const hashpasswd = crypto.createHash('sha256').update(data.get("password")).digest('base64');
    console.log({
      id: data.get("id"),
      password: data.get("password"),
    });
    try {
      // await async   
      const response = await axios.post(address + "/login",
        {
          user_id: data.get("id"),
          password: hashpasswd,
        },
        {withCredentials: true} //  cors  ,   
      );
      console.log(response);
      console.log(response.data);
      console.log(cookies);
      console.log(cookies.id);
      console.log(cookies.user_id);
      console.log("!undefined", !undefined);
      // setCookie('user_id', cookies.id);//   
      // console.log(cookies);
      // console.log(response.json());
      const isLogin = response.data.loginSuccess;
      // if (response.data.description !== undefinded)
      if(isLogin){ 
        // alert(" ");
        alert(document.cookie);
        console.log(document.cookie);
        navigate("/images");
      }
      else{alert(" ");}
    } catch (error) {
      console.error(error);
      alert(" !");
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box // div  
          sx={{
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar
            sx={{
              m: 1,
              bgcolor: "secondary.main",
            }} /*    profile  css     */
          >
            <LockOutlinedIcon />
          </Avatar>
          <Typography
            component="h1"
            variant="h5" /* h1    h5 ,       */
          >
            Bitwise
          </Typography>
          <Box
            component="form"
            onSubmit={handleSubmit}
            noValidate
            sx={{ mt: 1 }}
          >
            <TextField
              margin="normal"
              required //  
              fullWidth
              id="id"
              label="ID" //  
              name="id"
              autoComplete="id"
              autoFocus //    
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="current-password"
            />

            <Button
              type="submit"
              fullWidth
              variant="contained" //  , 
              sx={{ mt: 3, mb: 2 }} //  css, margintop, bottom
            >
              
            </Button>

            <Grid container /* container  */>
              <Grid item>
                <Link
                  href="/signup"
                  variant="body2"
                /* onClick={() => {
                  navigate("/");
                }} */
                >
                  {" ?   "}
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
        
      </Container>
    </ThemeProvider>
  );
}

export default SignIn;


