import React, { useState } from "react";
import styled from 'styled-components';
import { Button, Typography } from '@mui/material';
import axios from "axios";
import { useNavigate } from "react-router-dom";
import Checkbox from "@mui/material/Checkbox";

const address = "https://bitwise.ljlee37.com:8080";

const Wrapper = styled.div``;
function RemoveUser() {
    const user_id = "test";
    const navigate = useNavigate();
    const [checked, setChecked] = useState(false);
    const onClickCheckBox = (event) => {
        setChecked(event.target.checked);
    }
    const removeUser = async () => {
        if(checked){
            try {
                // await async   
                const response = await axios.delete(address + "/removeAccount",
                    {
                        user_id: user_id,
                    },
                    { withCredentials: true } //  cors  ,   
                );
                console.log(response);
                console.log(response.data);
    
                // console.log(response.json());
                const removeSuccess = response.data.removed;
                // if (response.data.description !== undefinded)
                if (removeSuccess) {
                    // alert(" ");
                    navigate("/");
                }
                else { alert(" (else)"); }
            } catch (error) {
                console.error(error);
                alert(" !(catch)");
            }
        } else{
            alert('    .')
        }
        
    }
    return (
        <Wrapper>
            <Typography variant="h5" gutterBottom>
                 
            </Typography>

            <Typography>
                  bitwise   .       
            </Typography>
            <Typography>
                <input type="checkbox" checked={checked}
                    onClick={onClickCheckBox} /> 
                {/* <Checkbox checked={checked}
                    onClick={onClickCheckBox} /> */}
            </Typography>

            <Button variant="outlined" onClick={removeUser}></Button>
        </Wrapper>

    );
}
export default RemoveUser;
